// components/LibroDigital.tsx
'use client';
import { useState } from 'react';

export default function LibroDigital({ textos }: { textos: Array<{ titulo: string; contenido: string }> }) {
  const [paginaActual, setPaginaActual] = useState(0);

  return (
    <div className="max-w-2xl mx-auto bg-white rounded-xl shadow-lg p-8">
      <div className="min-h-[400px] flex flex-col justify-between">
        <div>
          <h2 className="text-2xl font-bold mb-4">{textos[paginaActual].titulo}</h2>
          <p className="text-lg leading-relaxed whitespace-pre-line">
            {textos[paginaActual].contenido}
          </p>
        </div>
        
        <div className="flex justify-between items-center mt-8 pt-4 border-t">
          <button 
            onClick={() => setPaginaActual(prev => Math.max(0, prev - 1))}
            disabled={paginaActual === 0}
            className="px-4 py-2 bg-gray-100 rounded-lg disabled:opacity-50"
          >
            Anterior
          </button>
          
          <span className="text-sm text-gray-500">
            {paginaActual + 1} / {textos.length}
          </span>
          
          <button 
            onClick={() => setPaginaActual(prev => Math.min(textos.length - 1, prev + 1))}
            disabled={paginaActual === textos.length - 1}
            className="px-4 py-2 bg-gray-100 rounded-lg disabled:opacity-50"
          >
            Siguiente
          </button>
        </div>
      </div>
    </div>
  );
}